//MenuManager.ts
import { _decorator, Component, Node, director, Label } from 'cc';
import { EnumGameStatus, GameManager } from './GameManager';
const { ccclass, property } = _decorator;

@ccclass('MenuManager')
export class MenuManager extends Component {
    //暂停菜单
    @property(Node)
    pauseMenu:Node = null!
    //结算菜单
    @property(Node)
    endMenu:Node = null!
    //GameManager节点
    @property(GameManager)
    GameManagerScript:GameManager = null!
    //计分板节点
    @property(Label)
    private lb_score:Label = null!

    start() {
        //this.pauseMenu.active = false;//隐藏暂停菜单
        //this.endMenu.active = false;//隐藏结算菜单
    }
    onLoad(){
        this.pauseMenu.active = false;//隐藏暂停菜单
        this.endMenu.active = false;//隐藏结算菜单
    }

    clickReplayBtn(event:Event,CustomEventData){//游戏重新开始
        director.resume()//游戏场景恢复
        this.GameManagerScript.gameStatus=EnumGameStatus.init;
    }
    clickPauseBtn(event:Event,CustomEventData){//暂停游戏
        director.pause()//游戏场景暂停
        this.pauseMenu.active = true;
    }
    clickResumeBtn(event:Event,CustomEventData){//恢复游戏
        director.resume()//游戏场景恢复
        this.pauseMenu.active = false;//关闭菜单
    }
    changeScene(event:Event,CustomEventData){//切换场景
        director.loadScene(CustomEventData);
    }
    gameEnd(){//游戏结束
        director.pause()//游戏场景暂停
        this.endMenu.active = true;//激活节点
        this.lb_score.string = `您的分数为:${this.GameManagerScript._score}`//修改文本
    }
}
